import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@mui/styles';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import PlaceHolder from './PlaceHolder';

const styles = createStyles({
  subTitle: {
    fontSize: '24px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
    fontFamily: 'Gravity',
    marginTop: '16px',
  },
  contentText1: {
    fontSize: '14px',
    lineHeight: 1.2,
    // paddingRight: '40px',
    fontFamily: 'Gravity',
  },
  contentText2: {
    fontSize: '14px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  contentText3: {
    fontWeight: 800,
    fontSize: '16px',
    color: '#111111',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  experienceLogo: {
    width: '60%',
    fontFamily: 'Gravity',
    userSelect: 'none',
    userDrag: 'none',
  },
  contentNode: {
    margin: '0 0 0px 0',
    fontFamily: 'Gravity',
  },
  contextA: {
    color: '#555555',
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Experience extends Component<Props> {
  render() {
    const { classes } = this.props;
    const experienceNode =
      <React.Fragment>
        <Grid container>
          <Grid item md={8} sm={8} xs={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <div className={classes.subTitle}>{`# Experience`}</div>
            </Typography>
          </Grid>
        </Grid>
        <PlaceHolder />
        <Grid container spacing={4} justifyContent="flex-start" alignItems="center" className={classes.contentNode}>
          <Grid item md={3} sm={3} xs={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Feb. 2022 - Jun. 2022`}
            </Typography>
            <Typography variant="body2" gutterBottom align="right" className={classes.contentText1}>
              {`Beijing, China`}
            </Typography>
          </Grid>
          <Grid item md={7} sm={7} xs={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              <b>{`Research Intern, Software Engineer`}</b>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              <a className='contextA' href='https://icity.jd.com/english'>JD Intelligent Cities Research, Jing Dong Technology</a>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              Worked on visual analytics for urban computing, especially on intelligent transportation and disease control. Supervised by <a className='contextA' title='https://jie-bao.org/' href='https://jie-bao.org/'>Dr. Jie Bao</a> and <a className='contextA' title='http://urban-computing.com/yuzheng' href='http://urban-computing.com/yuzheng'>Prof. Yu Zheng</a>.
            </Typography>
          </Grid>
          <Grid item md={2} sm={2} xs={2}>
            <img alt="Jing Dong Technology" src="./logo/jdt.svg" className={classes.experienceLogo} />
          </Grid>
        </Grid>
        <PlaceHolder />
        <Grid container spacing={4} justifyContent="flex-start" alignItems="center" className={classes.contentNode}>
          <Grid item md={3} sm={3} xs={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Sept. 2019 - Dec. 2021`}
            </Typography>
            <Typography variant="body2" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7} sm={7} xs={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              <b>{`Research Intern`}</b>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
            <a className='contextA' title='http://www.idi.zju.edu.cn/en/space/2753.html' href='http://www.idi.zju.edu.cn/en/space/2753.html'>IDEA Lab</a>, <a title='https://azft.alibaba.com/' className='contextA' href='https://azft.alibaba.com/'>Alibaba-Zhejiang University Joint Research Institute of Frontier Technology</a>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              Supervised by <a className='contextA' title='http://www.ycwu.org/' href='http://www.ycwu.org/'>Prof. Yingcai Wu</a>. Coorperated with <a className='contextA' title='https://damo.alibaba.com/?lang=en' href='https://damo.alibaba.com/?lang=en'>DAMO Academy</a> and <a title='https://www.cainiao.com/en/index.html' className='contextA' href='https://www.cainiao.com/en/index.html'>Cainiao</a>.
            </Typography>
          </Grid>
          <Grid item md={2} sm={2} xs={2}>
            <img alt="Alibaba-Zhejiang University Joint Research Institute of Frontier Technology" src="./logo/azft.png" className={classes.experienceLogo} />
          </Grid>
        </Grid>
        <PlaceHolder />
        <Grid container spacing={4} justifyContent="flex-start" alignItems="center" className={classes.contentNode}>
          <Grid item md={3} sm={3} xs={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Nov. 2019 - Dec. 2021`}
            </Typography>
            <Typography variant="body2" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7} sm={7} xs={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              <b>{`Research Intern`}</b>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              <a className='contextA' title='https://www.cainiao.com/en/index.html' href='https://www.cainiao.com/en/index.html'>Cainiao Smart Logistics, Alibaba Group</a>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              {`Worked on visual analytics for intelligent logistics, especially on order processing monitoring and optimization.`}
            </Typography>
          </Grid>
          <Grid item md={2} sm={2} xs={2}>
            <img alt="Alibaba Group" src="./logo/alibaba.png" className={classes.experienceLogo} />
          </Grid>
        </Grid>
        <PlaceHolder />
        <Grid container spacing={4} justifyContent="flex-start" alignItems="center" className={classes.contentNode}>
          <Grid item md={3} sm={3} xs={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Nov. 2019 - Sept. 2020`}
            </Typography>
            <Typography variant="body2" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7} sm={7} xs={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              <b>{`Research Intern`}</b>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              <a className='contextA' title='https://damo.alibaba.com/?lang=en' href='https://damo.alibaba.com/?lang=en'>Alibaba DAMO Academy for Discovery, Adventure, Momentum and Outlook, Alibaba Group</a>
            </Typography>
            <Typography variant="body2" gutterBottom align="left" className={classes.contentText2}>
              {`Worked on visualization techniques and theory for intelligent data video generation.`}
            </Typography>
          </Grid>
          <Grid item md={2} sm={2} xs={2}>
            <img alt="Alibaba DAMO Academy for Discovery, Adventure, Momentum and Outlook" src="./logo/damo.svg" className={classes.experienceLogo} />
          </Grid>
        </Grid>
        <PlaceHolder />
      </React.Fragment>
    return (
      <div className="experience" id="experience">
        {experienceNode}
      </div>
    )
  }
}

export default withStyles(styles)(Experience);
