import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@mui/styles';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
// import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import SchoolIcon from '@mui/icons-material/School';

const styles = createStyles({
  header: {
    color: 'brown',
    paddingTop: '60px',
    width: '100%',
    fontFamily: 'Gravity',
  },
  headerNameE: {
    color: '#000000',
    fontWeight: 800,
    fontFamily: 'Gravity',
  },
  headerNameC: {
    color: '#aaaaaa',
    fontWeight: 400,
    marginLeft: '20px',
    fontSize: '24px'
  },
  headerBtn: {
    fontWeight: 400,
    marginLeft: '18px',
    fontSize: '18px',
  },
  headerIconA: {
    top: '6px',
    position: 'relative',
  }
})

type Props = {
  classes: {
    [propname: string]: string
  }
}

class Header extends Component<Props> {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.header}>
        <Grid container spacing={{md: 6, sm: 2, xs: 2}}>
          <Grid item md={12} sm={12} xs={12}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <span className={classes.headerNameE}>Junxiu Tang</span>
              <span className={classes.headerNameC}>()</span>
              <span className={classes.headerBtn}><a href='https://scholar.google.com/citations?user=iFvrE_IAAAAJ' title='Google Scholar' className={classes.headerIconA}><SchoolIcon /></a></span>
              <span className={classes.headerBtn}><a href='./cv.pdf' title='Curriculum Vitae' style={{textDecoration: "underline"}}>CV</a></span>
            </Typography>
          </Grid>
          {/* <Grid item md={2}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <AccountCircleIcon></AccountCircleIcon>
            </Typography>
          </Grid> */}
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(Header);
