import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@mui/styles';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import BusinessIcon from '@mui/icons-material/Business';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';

const styles = createStyles({
  subTitle: {
    fontSize: '24px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
    fontFamily: 'Gravity',
    marginTop: '16px',
  },
  contentText1: {
    fontSize: '14px',
    lineHeight: 1.2,
    // paddingRight: '40px',
    fontFamily: 'Gravity',
  },
  contentText2: {
    fontSize: '16px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  educationLogo: {
    width: '100%',
  },
  contentNode: {
    margin: '0 0 0px 0',
    justifyContent: 'flex-start',
  },
  publicationTeaserContainer: {
    width: '100%',
    height: '140px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
    fontFamily: 'Gravity',
  },
  publicationTeaser: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
  },
  publicationTitle: {
    fontWeight: 800,
    fontSize: '16px',
    color: '#353535',
    fontFamily: 'Gravity',
  },
  publicationAuthors: {
    color: '#555555',
    fontFamily: 'Gravity',
  },
  publicationIconText: {
    fontSize: '16px',
    margin: '0 5px',
    position: "relative",
    top: '-6px',
    fontFamily: 'Gravity',
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Contact extends Component<Props> {
  render() {
    const { classes } = this.props;
    const addMap = "https://www.google.com/maps/place/%E8%92%99%E6%B0%91%E4%BC%9F%E6%A5%BC/@30.4286373,120.6077147,9z/data=!4m15!1m7!3m6!1s0x344b63bfaf22a6e5:0x37ba33636279238d!2z5rWZ5rGf5aSn5a2m57Sr6YeR5riv5qCh5Yy6!8m2!3d30.305014!4d120.092069!16s%2Fm%2F047f1y_!3m6!1s0x344b63be1955dbbd:0x3c74f5f8e19bbe09!8m2!3d30.3033356!4d120.0898817!15sCnhSbS4zMjMsIE1lbmdtaW53ZWkgQmQuLCBaaWppbmdhbmcgQ2FtcHVzIG9mIFpoZWppYW5nIFVuaXZlcnNpdHkgTm8uODY2IFl1aGFuZ3RhbmcgUmQuLCBYaWh1IERpc3RyaWN0LCBIYW5nemhvdSwgWmhlamlhbmciA4gBAZIBD29mZmljZV9zZXJ2aWNlc-ABAA!16s%2Fg%2F12cpd220n";
    const contactNode =
      <div>
        <Grid container>
          <Grid item md={8} sm={8} xs={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <div className={classes.subTitle}>{`# Contact`}</div>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={4} justifyContent="flex-start" alignItems="center" className={classes.contentNode}>
          <Grid item md={1} sm={1} xs={1} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText2}>
              <BusinessIcon />
            </Typography>
          </Grid>
          <Grid item md={11} sm={11} xs={11} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              <a className='contextA' href={addMap}>Rm.323, Mengminwei Bd., Zijingang Campus of Zhejiang University</a>
              <br />
              <a className='contextA' href={addMap}>No.866 Yuhangtang Rd., Xihu District, Hangzhou, Zhejiang. 310058</a>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={4} justifyContent="flex-start" alignItems="center" className={classes.contentNode}>
          <Grid item md={1} sm={1} xs={1} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText2}>
              <AlternateEmailIcon />
            </Typography>
          </Grid>
          <Grid item md={11} sm={11} xs={11} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`tangjunxiu@zju.edu.cn`}
            </Typography>
          </Grid>
        </Grid>

      </div>
    return (
      <div className="contact" id="contact">
        {contactNode}
      </div>
    )
  }
}

export default withStyles(styles)(Contact);
